@import './variables.scss';
@import './mixins.scss';

.converterSelect {
	display: flex;
	justify-content: center;
	align-items: center;
	box-shadow: 0 0 2px $label-bgc;
}
.converterWrapper {
	display: flex;
	justify-content: center;
	align-items: center;
}
.converter {
	width: 40%;
	background-color: #fafafa;
	border-radius: 9px;
}
.converterLabel {
	background-color: $label-bgc;
	padding: 7px 9px;
	border-radius: 9px 9px 0 0;

	h3 {
		text-align: center;
		color: $label-color;
		margin: 0;
	}
}
.converterContent {
	padding: 30px 20px 20px;
}

.converterBlock {
	display: flex;
	justify-content: space-between;

	// &:first-of-type,
	// &:nth-of-type(2) {
	// 	margin-bottom: 25px;
	// }
	&.blocksDevide {
		position: relative;
		padding: 25px 0;

		svg {
			width: 100%;
		}
		img {
			position: absolute;
			top: 50%;
			bottom: 50%;
			right: 50%;
			left: 50%;
			@include size(32px);
		}
	}
	.converterInput {
		padding: 7px 9px;
		border: none;
		transition: box-shadow 0.3s ease;
		box-shadow: 0 0 3px $header-bgc;

		&:focus-visible {
			box-shadow: 0 0 3px 1px$header-bgc;
			outline: none;
		}
	}
}
